.TodoSearch{
   background-color: #F9FBFC;
   border-radius: 2px;
   border: 2px solid #202329;
   margin: 0 24px;
   height: 64px;
   width: calc(100% - 62px);
   font-size: 24px;
   text-align: center;
   font-family:'Courier New', Courier, monospace;
   font-weight: 400;
   color: #1E1E1F;
   box-shadow: 0px 5px 5px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
   color: #A5A5A5;
   font-family: 'Courier New', Courier, monospace;
   font-weight: 400;
}

.TodoSearch:focus {
   outline-color: #61DAFA;
}